$backgroundGradient: linear-gradient(to top left, black, #938301);

/* Общие стили для .advantages */
.advantages {
  margin-top: 100px;
  color: white;
}

/* Стили для .advantages_top */
.advantages .advantages_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Стили для .payment */
.advantages .advantages_top .payment {
  text-align: center;
  background: $backgroundGradient;
  width: 45%;
  height: 220px;
  border-radius: 10px;
  padding: 35px;
}

.advantages .advantages_top .payment p {
  font-size: 16px;
  color: white;
}

/* Стили для .advantages_top_email */
.advantages .advantages_top .advantages_top_email {
  text-align: center;
  width: 45%;
}

.advantages .advantages_top .advantages_top_email input {
  width: 400px;
  height: 30px;
  border: none;
  background: none;
  border-bottom: 1px solid #ffffff;
  font-size: 18px;
  color: white;
}

.advantages .advantages_top .advantages_top_email input:focus {
  outline: none;
}

.advantages .advantages_top .advantages_top_email button {
  background: $backgroundGradient;
  width: 400px;
  height: 40px;
  margin-top: 20px;
  color: white;
  border-radius: 30px;
  font-size: 14px;
}

.advantages .advantages_top .advantages_top_email button:hover {
  color: black;
}

/* Медиазапросы для разных экранов */
@media (max-width: 1000px) {
  .advantages .advantages_top .payment {
    width: 400px;
    height: 200px;
    padding: 25px;
  }

  .advantages .advantages_top .payment h2 {
    font-size: 20px;
  }

  .advantages .advantages_top .payment p {
    font-size: 14px;
  }

  .advantages .advantages_top .advantages_top_email input {
    width: 300px;
  }

  .advantages .advantages_top .advantages_top_email button {
    width: 300px;
    height: 40px;
  }
}

@media (max-width: 768px) {
  .advantages .advantages_top .payment {
    width: 350px;
    height: 170px;
    padding: 20px;
  }

  .advantages .advantages_top .payment h2 {
    font-size: 18px;
  }

  .advantages .advantages_top .advantages_top_email h3 {
    font-size: 16px;
  }

  .advantages .advantages_top .advantages_top_email p {
    font-size: 12px;
  }

  .advantages .advantages_top .advantages_top_email input {
    width: 250px;
    height: 20px;
    font-size: 16px;
  }

  .advantages .advantages_top .advantages_top_email button {
    width: 200px;
    height: 30px;
  }
}

@media (max-width: 576px) {
  .advantages .advantages_top .payment {
    width: 350px;
    height: 170px;
    padding: 20px;
  }

  .advantages .advantages_top .payment h2,
  .advantages .advantages_top .payment p {
    font-size: 18px;
  }

  .advantages .advantages_top .advantages_top_email h3 {
    font-size: 14px;
  }

  .advantages .advantages_top .advantages_top_email p {
    font-size: 12px;
  }

  .advantages .advantages_top .advantages_top_email input {
    width: 200px;
    height: 20px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .advantages .advantages_top {
    flex-direction: column;
  }

  .advantages .advantages_top .payment {
    width: 40%;
  }

  .advantages .advantages_top .advantages_top_email {
    margin-top: 30px;
    width: 40%;
  }

  .advantages .advantages_top .advantages_top_email h3 {
    font-size: 14px;
  }

  .advantages .advantages_top .advantages_top_email p {
    font-size: 12px;
  }
}
