.content {
  background-color: #000000;

}

.content_center {
  background-image: url("../img/backgrond.svg");
  max-width: 900px;
  min-height: 800px;
  margin-top: 20px;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-between;

}

.content_text {
  width: 45%;
   h1 {
    font-size: 32px;
  }
   p {
    font-size: 24px;
  }
}

.form {
  margin-left: 10px;
  background-color: white;
  height: 500px;
  width: 45%;
  border-radius: 15px;
  color: black;
  padding: 20px;

  .form_input {
    p {
      font-size: 12px;
      margin-bottom: 5px;
    }
    input {
      height: 50px;
      width: 300px;
      font-size: 18px;
      border: none;
      border-bottom: 1px solid #4b4b4b;
    }
    input:focus {
      outline: none;
    }
  }

  button {
    height: 50px;
    width: 120px;
    border-radius: 25px;
    color: white;
    font-size: 16px;
  }
  button:hover {
    color: black;
  }
}

/* Медиазапросы для разных экранов */
@media (max-width: 1000px) {
  .content {
    width: 1000px;
  }
  .content_center {
    width: 800px;
    min-height: 800px;
    margin-top: 20px;
    max-width: 900px;
    background-size: 1000px 800px;
  }
  .content_text h1 {
    font-size: 28px;
  }
  .content_text p {
    font-size: 22px;
  }
  .form {
    height: 500px;
    width: 350px;
  }
}


@media (max-width: 768px) {
  .content {
    width: 768px;
  }
  .content_center {
    width: 700px;
    flex-direction: column;
    background-size: 768px 800px;
  }
  .content_text {
    margin-top: 50px;
    h1 {
      font-size: 20px;
    }
  }
  .form {
    height: 400px;
    h3 {
      font-size: 18px;
    }
    p {
      font-size: 14px;
    }
    .form_input {
      input {
        height: 20px;
        font-size: 16px;
      }
    }
  }
}

@media (max-width: 576px) {
  .content {
    width: 576px;
  }
  .content_center {
    width: 576px;
    background-size: 576px 800px;
  }
  .content_text {
    margin-top: 50px;
    h1 {
      font-size: 20px;
    }
  }
  .form {
    height: 400px;
    width: 300px;
  }
}

@media (max-width: 480px) {
  .content {
    width: 480px;
    height: 900px;
  }
  .content_center {
    width: 480px;
    background-size: 480px 800px;
  }
  .content_text {
    margin-top: 50px;
    width: 350px;
    h1 {
      font-size: 20px;
    }
  }
  .form {
    margin-top: 30px;
    height: 400px;
    width: 320px;
  }
}

