.catalog {
  color: white;
  margin-top: 50px;
}

.advantages_icon_top {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 50px;

  div{
    width: 30%;

    h3{
      font-size: 18px;
    }

    img{
      width: 70%;
    }
  }

}

 .advantages_icon {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
}

 .advantages_icon div {
  text-align: center;
  width: 20%;
}

 .advantages_icon div img {
  width: 70%;
}

.catalog h1 {
  text-align: center;
}

 .catalog_production {
  margin-top: 50px;
}

 .catalog_image {
  margin-top: 70px;
}

 .catalog_image_cart {
  text-align: center;
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-top: 30px;

   img {
     transition: 1s;
     width: 45%;
   }

   img:hover {
     transform: scale(1.03);
   }
   p{
     width: 50%;
     font-size: 20px;
     text-align: left;
   }
}


#advantages {

  h2{
    font-size: 28px;
  }
  p {
    font-size: 24px;
  }
}



@media (max-width: 1000px) {
   .catalog_image {
    margin-top: 30px;
  }

   .catalog_image_cart img {
    width: 400px;
  }

  .advantages_icon_top div h3{
    font-size: 16px;
  }

  #catalog h1{
    font-size: 28px;
  }

  #advantages {
    h2{
      font-size: 18px;
    }

    p{
      font-size: 12px;
    }

  }


}

@media (max-width: 768px) {
   .catalog_image {
    margin-top: 30px;
  }

   .catalog_image_cart img {
    width: 300px;
  }
}

@media (max-width: 576px) {
   .advantages_icon div img {
    width: 70px;
  }


   .catalog_image_cart img {
    width: 200px;
  }

   .catalog_image_cart p {
    width: 200px;
  }
}
