.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.header .logo {
  font-size: 36px;
  font-weight: initial;
  color: #f5e006;
}

.header .navigation a {
  color: white;
  margin-left: 20px;
}

.header .img_icon {
  margin-right: 10px;

}

.header .img_icon a img {
  height: 35px;
  margin-left: 10px;
}

.header .contacts {
  display: flex;
  align-items: center;
}

.header .contacts .adress {
  margin-left: 20px;
  color: white;
  align-items: center;
}



.header .contacts .adress a {
  margin: 0;
  color: white;
  margin-top: 5px;
}


/* Медиазапрос для уменьшения размеров на средних экранах */
@media (max-width: 1120px) {
  .header .logo {
    font-size: 24px;
  }

  .header .navigation {
    display: none;
  }

  .header .img_icon {
    margin-right: 50px;
    display: flex;
  }

  .header .contacts {
    display: flex;
    align-items: center;
  }

  .header .contacts .adress a {
    font-size: 16px;
  }

}


@media (max-width: 980px) {
  .header{
    margin-top: 30px;
  }
  .header .logo {
    font-size: 45px;
  }
  .header .img_icon a img {
    height: 50px;
    margin-left: 15px;
  }
  .header a img {
    height: 50px;
  }
  .header .contacts .adress {
    a{
      font-size: 18px;
    }
    p{
      font-size: 18px;
    }
  }
}

@media (max-width: 576px) {
  .header .logo {
    font-size: 28px;
  }

  .header .contacts .adress {
    display: none;
  }
}

@media (max-width: 480px) {
  .header .contacts .adress .number {
    display: none;
  }
}
